import React from 'react'

import { useWeb3React } from '@web3-react/core'
import { Web3Provider } from '@ethersproject/providers'

import * as Token from '../models/token'
import config from '../config'

import Loading from './loading'

type NumberToStringDictionary = {[key: number]: string} // token id => owner

type Props = {
  tokens: Token.Token[] | undefined
  owners: NumberToStringDictionary | undefined
  claimers: NumberToStringDictionary | undefined
  onRedeem: (tokenId: number) => any
}

const Showcase: React.FC<Props> = (props: Props) => {
  const context = useWeb3React<Web3Provider>()
  const { account } = context

  const { tokens, claimers, owners, onRedeem } = props

  if (!tokens) {
    return <Loading/>
  }

  const doNothing = (e: React.MouseEvent<HTMLAnchorElement>) => {
    e.stopPropagation();
    return e
  }

  return (
    <section id="redemption2-section">
      <div id="container01" className="container columns full screen" style={{minHeight: 'auto'}}>
        <div className="wrapper">
          <div className="inner" style={{ display: "flex" }}>
            {/* <div>
              <h2 id="text01">These are the salt seasons you own</h2>
            </div> */}
            { tokens!.map(token => {
              const theOwner = (owners || {})[token.tokenId]
              const theClaimer = (claimers || {})[token.tokenId]

              const isOwner = !!account && !!theOwner && theOwner === account

              return (
                <div style={{ width: "calc(30% + 1rem)", marginLeft: 0 }} key={token.tokenId}>
                  <div id="image01" className="image" data-position="center">
                    <span className="deferred">
                      <img 
                        // src="data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHg9IjAiIHk9IjAiIHZpZXdCb3g9IjAgMCA1ODYgNzM4IiB3aWR0aD0iNTg2IiBoZWlnaHQ9IjczOCIgcHJlc2VydmVBc3BlY3RSYXRpbz0ibm9uZSI%2BPHJlY3QgZmlsbD0idHJhbnNwYXJlbnQiIHdpZHRoPSIxMDAlIiBoZWlnaHQ9IjEwMCUiLz48L3N2Zz4%3D"
                        // data-src={token.imageUrl}
                        src={token.imageUrl}
                        alt=""
                      />
                    </span>
                  </div>
                  <ul id="buttons03" className="style1 buttons">
                    <li>
                    {
                      isOwner ? (
                        theClaimer ? (
                          <a href="#" className="button n01" onClick={doNothing}>
                            <span className="label">Already redeemed</span>
                          </a>
                        ) : (
                          <a href="#" className="button n01" onClick={event => {
                            event.stopPropagation()
                            onRedeem(token.tokenId)
                            return false
                          }}>
                            <svg><use href="assets/icons.svg#download"></use></svg><span className="label">Claim</span>
                          </a>
                        )
                      ) : (
                        (!!theOwner && theOwner !== config.minterAddress) ? (
                          <a href="#" className="button n01" onClick={doNothing}>
                            <span className="label">Sold</span>
                          </a>
                        ) : (
                          <a href="https://opensea.io/" target="_blank" className="button n01">
                            <span className="label">Buy</span>
                          </a>
                        )
                      )
                    }
                    </li>
                  </ul>
                </div>
              )
            })}
          </div>
        </div>
      </div>
    </section>)
}

export default Showcase